import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController,
  ModalController
} from "ionic-angular";
import { ConfigColIndexLoopViewModel } from "../../Model/ViewModel/ConfigColIndexLoopViewModel";
import { GlobalComponent } from "../../components/global/global";
import { ConfigColIndexLoopServicesProvider } from '../../providers/config-col-index-loop-services/config-col-index-loop-services';

/**
 * Generated class for the ConfigColIndexLoopModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-config-col-index-loop-add-edit-modal",
  templateUrl: "config-col-index-loop-add-edit-modal.html"
})
export class ConfigColIndexLoopAddEditModalPage {
  item: ConfigColIndexLoopViewModel;
  mode: string;
  title: string;
  CanEditBatch: boolean;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public modalCtrl: ModalController,
    private global: GlobalComponent,
    public ConfigColIndexLoopServices:ConfigColIndexLoopServicesProvider,
  ) {
    this.item = new ConfigColIndexLoopViewModel();
    this.item.table_index_seq=navParams.data.item.table_index_seq;
this.item.schemaname=navParams.data.item.schemaname;
this.item.tablename=navParams.data.item.tablename;
this.item.columnname=navParams.data.item.columnname;
this.item.job01=navParams.data.item.job01;
this.item.job02=navParams.data.item.job02;
this.item.job03=navParams.data.item.job03;
this.item.col_index=navParams.data.item.col_index;
this.item.creator=navParams.data.item.creator;
this.item.create_time=navParams.data.item.create_time;
this.item.modifier=navParams.data.item.modifier;
this.item.last_update_time=navParams.data.item.last_update_time;


    this.mode = navParams.data.mode;
    this.CanEditBatch = navParams.data.CanEditBatch;
    if (this.mode === "POST")
    {
      this.title = "新增";
      this.Get_max_table_index_seq();
    }
    else this.title = "更新";
  }
  Get_max_table_index_seq() {

    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.ConfigColIndexLoopServices.Get_max_table_index_seqAsync
        (this.item.schemaname,this.item.tablename)
        .subscribe(
          data => {

            if (data.DidError === true)
            {
              this.global.dismissLoading();
             this.global.showError(data.ErrorMessage);
            }
            else {
             this.item.table_index_seq=data.Model;
              this.global.dismissLoading();
            }
          },
          err => {
            this.global.dismissLoading();
            this.global.showError("無法連上WebAPI伺服器-"+err.message);
          }
        );

    });
  }
  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad ConfigColIndexLoopModalPage");
  }
}



