import {StringResponse} from '../../Model/Response/StringResponse';
import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { ConfigColIndexLoopListResponse } from "../../Model/Response/ConfigColIndexLoopListResponse";
import { ConfigColIndexLoopResponse } from "../../Model/Response/ConfigColIndexLoopResponse";
import { ConfigColIndexLoopViewModel } from '../../Model/ViewModel/ConfigColIndexLoopViewModel';

/*
  Generated class for the ConfigColIndexLoopServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class ConfigColIndexLoopServicesProvider {
  private ConfigColIndexLoops: ConfigColIndexLoopListResponse;
  private ConfigColIndexLoop: ConfigColIndexLoopResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "ConfigColIndexLoop";
    console.log("Hello ConfigColIndexLoopServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number,schemaname: string, tablename: string,columnname:string, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "ConfigColIndexLoop?PageSize={1}&PageNumber={2}&schemaname={3}&tablename={4}&columnname={5}&keyword={6}&order_type={7}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber,schemaname, tablename,columnname, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.ConfigColIndexLoops = new ConfigColIndexLoopListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.ConfigColIndexLoops;
    });
  }



  public PostPutAsync(ConfigColIndexLoopViewModel:ConfigColIndexLoopViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "ConfigColIndexLoop";
    else
    sub_url = "ConfigColIndexLoop/{1}";
    sub_url = String.Format(sub_url, this.ctl,ConfigColIndexLoopViewModel.table_index_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,ConfigColIndexLoopViewModel,mode).map(
      item => {
        this.ConfigColIndexLoop = new ConfigColIndexLoopResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.ConfigColIndexLoop;
      }
    );
  }

  public DeleteAsync(table_index_seq:string) {
    var sub_url: string;
    sub_url = "ConfigColIndexLoop/{1}";
    sub_url = String.Format(sub_url, this.ctl,table_index_seq);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.ConfigColIndexLoop = new ConfigColIndexLoopResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.ConfigColIndexLoop;
      }
    );
  }
  public Get_max_table_index_seqAsync(schemaname:string,tablename:string) {
    var sub_url: string;
    sub_url = "ConfigColIndexLoop/Get_max_table_index_seq?schemaname={1}&tablename={2}";
    sub_url = String.Format(sub_url, this.ctl,schemaname,tablename);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }


  public GetSqlAsync(table_index_seq:string,method:string) {
    var sub_url: string;
    sub_url = "ConfigColIndexLoop/get_{2}_sql/{1}";
    sub_url = String.Format(sub_url, this.ctl,table_index_seq,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
}



