import { SingleModelResponse } from "./SingleModelResponse";
import { ConfigColIndexLoopViewModel } from "../ViewModel/ConfigColIndexLoopViewModel";

export class ConfigColIndexLoopResponse extends SingleModelResponse<
  ConfigColIndexLoopViewModel
> {
  Model: ConfigColIndexLoopViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new ConfigColIndexLoopViewModel();
      this.Model.table_index_seq=obj.Model.table_index_seq;
      this.Model.schemaname=obj.Model.schemaname;
      this.Model.tablename=obj.Model.tablename;
      this.Model.columnname=obj.Model.columnname;
      this.Model.job01=obj && obj.Model.job01 || null;
      this.Model.job02=obj && obj.Model.job02 || null;
      this.Model.job03=obj && obj.Model.job03 || null;
      this.Model.col_index=obj.Model.col_index;
      this.Model.creator=obj.Model.creator;
      this.Model.create_time=obj.Model.create_time;
      this.Model.modifier=obj.Model.modifier;
      this.Model.last_update_time=obj.Model.last_update_time;

    }
  }
}



